.caculator {
    margin: 300px auto 0;
    width: auto;
    border: 1px solid black;
    border-radius: 15px;
    padding: 30px;
}
.caculator__input {
    width: 100%;
    height: 50px;
    font-size: 20px;
    text-align: right;
}
.caculator__input:focus {
    outline: none;
}
.caculator__key {
    margin-top: 30px;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(5, 1fr);
}
.caculator__key button {
    width: 50px;
    height: 50px;
    border: none;
    transition: all .5s;
}
.caculator__key button:focus {
    outline: none;
    box-shadow: 2px 2px 1px 0 rgba(0,0,0,0.3);
    transform: scale(0.9);
}
.caculator__key button:nth-child(15) {
    grid-area: 3 / 5 / 5 / 6;
    height: 100%;
}
.caculator__key button:nth-child(16) {
    grid-row: 4 / 5;
    grid-column: 1 / 3;
    width: 100%;
}
